<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('ManageLayoutTitle') }}</span>                    
                </div>
                <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-ExternalLayoutLink="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text" style="height:35px;">{{HttpText}}//</span>
                                        <input class="form-control exteranllink text-truncate" name="ExternalLayoutLink" placeholder="http://abc.com/PostApplicant" type="text">                                                                              
                                        <span class="input-group-text" style="height:35px;">{{FHttpText}}</span>
                                    </div>
                                </div>
                            </template>
                            <template v-slot:tgslot-Header="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model=data.fieldInfo.value></ckeditor>
                                <span name="Header" class="invalid-feedback d-block">{{ data.error }}</span> 
                            </template>
                            <template v-slot:tgslot-Footer="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model=data.fieldInfo.value></ckeditor>
                                <span name="Footer" class="invalid-feedback d-block">{{ data.error }}</span> 
                            </template>
                            <template v-slot:tgslot-HomePageText="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model=data.fieldInfo.value></ckeditor>
                                <span name="Footer" class="invalid-feedback d-block">{{ data.error }}</span> 
                            </template>
                        </dynamic-form>
                            <div class="row">
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small class="text-danger">{{ $t('MandatoryString') }}</small>
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        </div> 
    </section>   
</template>
<script>
import DataService from '../../services/DataService';
export default { 
    data(){
        return{
            editor: this.CLASSIC_EDITOR,
            isLoading:false,
            FormSchema: [                
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Welcome Text',
                            name: "WelcomeText",
                            value: "",
                            placeholder: "You Are in Ticketing Module",
                            validationRules: { "required": false },
                        },     
                        {
                            astype: "ColorField",
                            label: this.$t('Background Color'),
                            name: "BackgroundColor",
                            value: "#FFFFFF",
                            placeholder: "",
                            validationRules: { "required": false },
                        },
                        {
                            astype: "ColorField",
                            label: this.$t('Font Color'),
                            name: "FontColor",
                            value: "#FFFFFF",
                            placeholder: "",
                            validationRules: { "required": false },
                        },  
                        {
                            astype: "SelectField",
                            label: this.$t('Font Size'),
                            name: "FontSize",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: "Select Font Size",
                            validationRules: { "required": false,},
                        },                
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Redirect To Url'),
                            name: "RedirectToUrl",
                            value: "",
                            placeholder: "http://abc.com/PostApplicant",
                            validationRules: { "required": false },                           
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('External Layout Link'),
                            name: "ExternalLayoutLink",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },                           
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Header Text'),
                            name: "Header",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Footer Text'),
                            name: "Footer",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Home Page Text'),
                            name: "HomePageText",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                }  
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },               
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-refresh pr-2'></i>Reset",
                    onClick: this.Reset
                },
                // {
                //     type: "submit",
                //     class: "btn btn-primary",
                //     isVisible:true,
                //     text: "<i class='fa fa-eye pr-2'></i>Preview",
                //     onClick: this.Update
                // },
            ],
            FontSize:36,
            HostName:'',
            HttpText:''
        }
    },
    created:function(){        
        this.GetFontSizeList(this.FontSize);
        this.FrontSiteUrlHostName();
    },
    methods:{
        Reset(formfields) {
            debugger
        },
        FrontSiteUrlHostName(){
            var url=`configkey=FrontSiteUrl`;
            DataService.FrontSiteUrlHostName(url).then(response=>{                 
                if(response.data.list.result.length>0){                   
                    this.HostName=response.data.list.result[0].configDataValue.split('/');
                    this.HttpText=this.HostName[0];
                    this.FHttpText=this.HostName[2];
                }
             });
        },
        GetFontSizeList(Count){            
            for (let i = 10; i <= Count; i += 2)
            {
                let obj={
                    name:i,
                    value:i
                } 
                this.FormSchema[0].Data[3].config.options.push(obj);
            }
        },
        onSubmit(formfields, val) {            
            var ths =this;
            ths.isLoading=true;           
            var jsonobj = {
                layoutid:0,
                Header: formfields.Header,
                Footer:formfields.Footer,
                BackgroundColor:formfields.BackgroundColor,
                FontColor:formfields.FontColor,
                FontSize:formfields.FontSize,
                WelcomeText: formfields.WelcomeText,                
                ExternalLayoutLink:formfields.ExternalLayoutLink,
                HomePageText: formfields.HomePageText, 
                RedirectToUrl:formfields.RedirectToUrl                
            };

            var params = JSON.stringify(jsonobj);
            DataService.SaveTicketManageLayout(params).then(response=>{                
                if(response.data.result=='ADD'){
                    ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("ManageLayoutTitle")]), "success", true, ths.$t("Alert"));
                }
                else if(response.data.result=='UPDATE'){
                    ths.ShowAlert(ths.$t("UpdatedSuccess", [ths.$t("ManageLayoutTitle")]), "success", true, ths.$t("Alert"));
                }else{
                    ths.ShowAlert(ths.$t("AddedError", [ths.$t("ManageLayoutTitle")]), "failure", true, ths.$t("Alert")); 
                }
                ths.isLoading=false;
            })
        }    
    }
}
</script>